import {getRandomInt} from './utils/index.js';

const title = [
  `To make the project`,
  `To do homework`,
  `To cook something`,
];

const tags = new Set([
  `homework`,
  `entertainment`,
  `myself`,
  `education`,
  `family`,
  `theory`,
  `practice`
]);

const setTags = function () {
  let arrTags = [];
  let arrNumber = [];
  let i = 0;
  while (i < 3) {
    let num = getRandomInt(7);
    if (arrNumber.indexOf(num) === -1) {
      arrTags[i] = tags[num];
      arrNumber[i] = num;
      i = i + 1;
    }
  }
  return arrTags;
};

export const task = {
  title: title[Math.floor(Math.random() * 3)],
  dueDate: Date.now() + 1 + Math.floor(Math.random() * 7) * 24 * 60 * 60 * 1000,
  picture: `//picsum.photos/100/100?r=${Math.random()}`,
  flagRepeat: true,
  repeatingDays: {
    'mo': true,
    'tu': false,
    'we': true,
    'th': false,
    'fr': false,
    'sa': true,
    'su': false,
  },
  tags: setTags(),
  colorType: [
    `black`,
    `green`,
    `yellow`,
    `pink`,
    `blue`
  ][Math.floor(Math.random() * 5)],
  isFavorite: false,
  isDone: false
};
